<template>
  <v-card v-if="user">
    <v-card-title>
      You are signed in!
    </v-card-title>
    <v-card-text>
      <p>id: {{ user.uid }}</p>
      <p>Email: {{ user.email }}</p>
    </v-card-text>
    <v-card-actions>
      <v-spacer></v-spacer>
      <v-btn color="error" class="text-capitalize" @click="signOut"
        >Sign Out</v-btn
      >
    </v-card-actions>
  </v-card>
</template>

<script>
/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
export default {
  data() {
    return {
      user: null,
    };
  },
  created() {
    this.user = this.$store.state.user;
  },
  methods: {
    signOut() {
      this.$store.dispatch("signOut");
    },
  },
};
</script>
